<template>
  <div>
    <el-row :gutter="24" type='flex'>
      <el-col :span="20">
        <div class="panel m-l-md">
          <panel-title :title="$lang.nlp.function">
          </panel-title>
          <div
              class="panel-body"
          >
            <el-row :gutter="24">
              <el-col :span="18">
                <el-form ref="form" :model="form" :rules="rules" label-width="100px">
                  <el-form-item :label="$lang.nlp.textinput" prop="text">
                    <el-input
                        type="textarea"
                        :rows="17"
                        v-model="form.text"
                        :placeholder="$lang.messages.enter + ' ' + $lang.nlp.textinput"
                    ></el-input>
                  </el-form-item>
                  <el-form-item :label="$lang.columns.output" prop="output">
                    <div id="canvas"
                         style="border:1px solid #e4e4e4;
                       width: 800px;height: 200px;border-radius: 0.5rem;">
                      <div v-text="info" v-if="showinfo" style="margin-left: 10px">
                      </div>
                    </div>
                  </el-form-item>

                </el-form>
              </el-col>
              <el-col :span="6">
                <el-form ref="form" :model="form" :rules="rules" label-width="100px">
                  <el-form-item :label="$lang.nlp.title" prop="title">
                    <el-input
                        v-model="form.title"
                        :placeholder="$lang.messages.enter + ' ' + $lang.nlp.title"
                        size="small"
                    ></el-input>
                  </el-form-item>
                  <el-form-item :label="$lang.nlp.type" prop="type">
                    <el-input
                        v-model="form.type"
                        :placeholder="$lang.messages.enter + ' ' + $lang.nlp.type"
                        size="small"
                    ></el-input>
                  </el-form-item>
                  <el-form-item>
                    <el-select
                        v-model="form.model"
                        filterable
                        allow-create
                        default-first-option
                        placeholder="Choose NLP model">
                      <el-option
                          v-for="item in options"
                          :key="item.label"
                          :label="item.label"
                          :value="item.label">
                      </el-option>
                    </el-select>
                  </el-form-item>

                  <el-form-item>
                    <el-button
                        type="primary"
                        size="medium"
                        style="width:100px"
                        @click="onSubmitForm"
                        :loading="onSubmitLoading"
                    >
                      <i class="fa fa-check"></i>
                      {{ $lang.nlp.run }}
                    </el-button>
                  </el-form-item>

                  <el-form-item>
                    <el-button @click="$router.go(0)" size="medium" style="width:100px">
                      <i class="fa fa-reply"></i>
                      {{ $lang.nlp.clear }}
                    </el-button>
                  </el-form-item>
                </el-form>
              </el-col>

            </el-row>
          </div>
        </div>
      </el-col>
      <el-col :span="4">
        <div class="panel" id="tree">
          <panel-title :title="$lang.nlp.nlpmodel"></panel-title>
          <div class="panel-body">
            <el-tree
                :data="tree"
                :props="defaultProps"
                :empty-text="$lang.messages.noData"
            ></el-tree>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import PanelTitle from "../../components/PanelTitle";
import axios from 'axios';
import {ip, port} from "../../utils/regex";

export default {
  data() {
    return {
      options: [],
      form: {
        text: "",
        title: "",
        type: "",
        model:"",
      },
      tree: [],
      info: {},
      showinfo: false,
      loadData: false,
      onSubmitLoading: false,
      activeNode: [],
      activeFile: null,
      rules: {
        text: [
          {
            required: true,
            message:
                this.$store.getters.$lang.nlp.textinput +
                " " +
                this.$store.getters.$lang.messages.isNull,
            trigger: "blur",
          },
        ],
        title: [
          {
            required: true,
            message:
                this.$store.getters.$lang.nlp.title +
                " " +
                this.$store.getters.$lang.messages.isNull,
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {
    this.getProjectTree(this.projectName);
  },
  methods: {
    getProjectTree(name) {
      // 获取目录树
      this.$http
          .get(
              this.formatString(this.$store.state.url.nlp.nlp_tree, {})
          )
          .then(({data: tree}) => {
            this.tree = tree.tree;
            this.options = tree.model
            console.log(tree);
          })
          .catch(() => {
            this.$message.error(this.$store.getters.$lang.messages.errorLoad);
          });
    },
    onSubmitForm() {
      this.$refs.form.validate((valid) => {
        if (!valid) return false;
        this.onSubmitLoading = true;
        this.$http
            .post(this.$store.state.url.nlp.run, this.form)
            .then(({data: info}) => {
              this.info = info;
              console.log(info);
              this.onSubmitLoading = false;
              this.showinfo = true;
            })
            .catch(() => {
              this.onSubmitLoading = false;
            });
      });
    },
  },
  components: {
    PanelTitle,
  },
};
</script>